<!-- 头部组件 -->
<template>
    <div class="header" :style="{background:bgColor}">
        <div class="header-left">
            <img :src="logo" class="logo" alt="" />
            <div class="user-system-name"><span>{{ systemName }}</span></div>
        </div>
        <ul class="header-system">
            <li v-for="item in subsystem">
                <router-link to="">{{ item.name }}</router-link>
            </li>
            <li><i class="iconfont icon-more" @click="subsystemShow"></i></li>
        </ul>
        <div v-show="layer" class="header-system-layer">
            <ul>
                <li v-for="item in subsystem" >
                    <router-link  to="" style="color: #ffffff">{{ item.name }}</router-link>
                </li>
            </ul>
            <div class="header-system-layer-i" @click="subsystemHide"><i class="iconfont icon-more"></i></div>
        </div>
        <div class="header-right" v-clickOutSide="loginHide">
            <div class="user-info" @click="loginShow">
                <img class="userPhoto" src="http://cdn.admui.com/demo/pjax/2.0.0/images/avatar.svg" style="margin-right: 15px;" alt="" />
                <div class="user-name">管理员</div>
                <i class="iconfont icon-arrow-down"></i>
            </div>
            <div class="heaer-right-arrow" :class="swithch?'':'active'" v-if="swithShow" @click="monoSwich"><i
                    class="iconfont icon-arrow-header" :class="swithch?'':'active'"></i></div>
            <div class="login-info" v-show="loginInfoShow">
                <ul>
                    <li @click="exit">退出登录</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'TopHead',
    data() {
        return {
            logo: require('@/assets/image/logo.png'),
            systemName: '元素智慧校园',
            systemName1Show:false,
            systemName1: 'demo副标题',
            bgColor:'#4A80F6',
            swithch: this.$store.state.offsideConfig.offsideFold,
            swithShow:false,
            loginInfoShow:false,
            subsystem:[
                {
                    name:'统一门户',
                    url:'',
                },
                {
                    name:'考勤系统',
                    url:'',
                },
                {
                    name:'身份中心',
                    url:'',
                },
                {
                    name:'请假系统',
                    url:'',
                },
                {
                    name:'人脸引擎',
                    url:'',
                },



            ],
            layer:false
        }
    },
    watch: {
        swithch(val) {
            if (val) {
                $('.note-card-wrap').stop().animate({
                    right: '0'
                }, 200)
            } else {
                $('.note-card-wrap').stop().animate({
                    right: '-340px'
                }, 200)
            }
        }
    },
    methods: {
        subsystemShow(){
            this.layer = true;
        },
        subsystemHide(){
            this.layer = false;
        },
        monoSwich() {
            this.swithch = !this.swithch;
        },
        loginShow(){
            this.loginInfoShow = true;
        },
        loginHide(){
            this.loginInfoShow = false;
        },
        exit(){
            let classSelect = {};
            this.$api.loginExit(classSelect).then(res => {
                if(res.success == true){
                    let toke=  localStorage.getItem('Authorization');
                    localStorage.clear(toke);
                    this.$router.push({
                        path: '/signin'
                    });
                }
            });
        }
    },
    mounted() {

    }
}
</script>

<style lang="scss" scoped>
    .header {
        width: 100%;
        height: 60px;
        line-height: 60px;
        background: #4a80f6;
        display: flex;
        align-items: center;
    }
    .user-info{
        display: flex;
        align-items: center;
        margin-right: 18px;
        overflow: hidden;
    }

    .login-info{
        position: absolute;
        right: 30px;
        top: 45px;
        background-color: #FFFFFF;
        z-index: 99;
        width: 120px;
        box-shadow:0 0 10px #ddd;
    }

    .login-info>ul>li{
        padding: 5px 12px;
        line-height: 33px;
        text-align: justify;
    }
    .logo {
        width: 29px;
        height: 21px;
        display: inline-block;
        margin-right: 10px;
    }
    .user-system-name {
        font-size: 20px;
        color: #ffffff;

        display: inline-block;
    }
    .user-system-name span{
        vertical-align: middle;
        font-size: 20px;
        color: #ffffff;
    }

    .system-name {
        font-size: 16px;
        color: #adc6fb;
        vertical-align: middle;
        display: inline-block;
    }

    .userPhoto {
        width: 30px;
        height: 30px;
    }

    .iconfont {
        font-size: 20px;
        color: #ffffff;
    }
    .header-right {
        display: flex;
        align-items: center;
        margin-left: auto;
        position: relative;
    }
    .header-right>i,
    .header-right>div {
        margin-left: 12px;
    }

    .heaer-right-arrow {
        display: inline-block;
        background-color: #4482d0;
    }
    .icon-arrow-header.active {
        transform: rotate(180deg);
        display: inline-block;
    }
    .icon-arrow-down{
        transform: rotate(90deg);
        display: inline-block;
        font-size: 16px;
        margin-left: 5px;
    }
    .user-name{
        color: #FFFFFF;
    }
    .header-left{
        padding-left: 20px;
        width: 200px;
    }
    .header-system{
        margin-left: 150px;
        width: 480px;
        display: flex;
    }
    .header-system>li{
        width: 80px;
        text-align: center;
    }
    .header-system>li>a,.header-system>li>i{
        color: #e5e5e5;
    }
    .header-system>li>a:hover {
        cursor: pointer;
        color: #ffffff;
    }
    .header-system-layer{
        width: 480px;
        height: auto;
        background: #000000;
        position: absolute;
        left: 350px;
        top: 0px;
        z-index: 999999;
        padding: 10px 0px;
    }
    .header-system-layer>ul{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 400px;
    }
    .header-system-layer>ul>li{
        text-align: center;
        width: 80px;
        height: 40px;
        line-height: 40px;
    }
    .header-system-layer-i{
        position: absolute;
        top: 0px;
        right: 30px;
    }
</style>
